import React, { useEffect } from 'react';
import Aos from 'aos';
import 'aos/dist/aos.css';
const About = () => {

  useEffect(() => {
    Aos.init({
      delay: 200,
      duration: 1200,
      once: false,
    })
  }, []);
  return (
    <div className="content " id="About-section">
      <div className="container text-center About-section  " >
        <p className="pt-5 fs-1 text-white  fw-bolder font-monospace " data-aos="zoom-in" >We Make Something Amazing!</p>
        <hr className="divider divider-style" />
        <p className="fs-5 text-center text-white ps-5 fw-light" data-aos="fade">Lorem ipsum dolor sit, ametad perferendis repudiandae maxime impedit voluptatibus. Lorem ipsum dolor sit, amet consectetur adipisicing elit. Error tempora voluptate sequi sed minima culpa iure ad consequuntur, fugit reprehenderit?</p>
        <div className="text-center">
          <button className="btn btn-light btn-font  btn-lg rounded-pill text-center mb-5 btn-style ">Primary Button</button>

        </div>
      </div>
    </div>
  )
}

export default About