import React from 'react';
import Hoverimg from './Hoverimg';
import portfolioSection from '../mockData/portfolioSection';
import { useEffect } from 'react';
import Aos from 'aos';
const Portfolio = () => {

  useEffect(() => {
    Aos.init({
      delay: 200,
      duration: 1200,
      once: false,
    })
  }, []);
  return (
    <div id="Portfolio-section">
      <div className="container-fluid p-0">
        <div className=" gy-2 d-flex  flex-wrap row">

          {

            portfolioSection.map((val) => (
              < Hoverimg key={val.id} imgsrc={val.imgsrc} title={val.title} description={val.description} />
            ))
          }
        </div>
      </div>
    </div>



  )
}

export default Portfolio