import React from 'react'

const Contact = () => {
  return (
    <div id="contact-section">
        <div class="container text-center ">
          <div class="row justify-content-center pt-5">
          <div class="col-lg-8 col-xl-6">
      <h2 class="class pt-5">Let's Get In Touch</h2>
          <hr class="divider-style divider-color"/>
          <p class="pb-5">Lorem ipsum dolor sit amet consectetur adipisicing elit. Sapiente nisi atque excepturi aperiam. Numquam itaque hic praesentium laudantium! Saepe, amet?</p>
           
          <form>

            <div class="mb-3">
              <input type="text" class="form-control" placeholder="Full Name"  id="" aria-describedby="emailHelp" required/>
            </div>


            <div class="mb-3">
              <input type="email" class="form-control" placeholder="Email Address" id="exampleInputEmail1" aria-describedby="emailHelp" required />
            </div>

            <div class="mb-3">
              <input type="password" class="form-control" placeholder="Phone number" id="exampleInputPassword1" />
            </div>

            <div class="mb-3">
              
              <textarea class="form-control" id="exampleFormControlTextarea1" placeholder="Message"  rows="3"></textarea>
            </div>

            <div class="d-grid">
              <button type="submit" class="btn btn-submit rounded-pill btn-lg btn-danger">Submit</button>
            </div>
           
            

            
          </form>
        </div>
      </div>
        </div>
      </div>

      
  )
}

export default Contact