import React from 'react'

const Hoverimg = (props) => {
    return (
        <div className=' col-sm-12 col-md-4'>
            <div className=" overlay-box   " data-aos="flip-left">
                <img src={props.imgsrc} alt="" className='portfolio  w-100 h-100 ' />
                <div className="overlay-box-content  ">
                    <div className="overlay-content mx-2">

                        <h5 className="text-white-50">{props.title}</h5>
                        <h4 className="text-white ">{props.description}</h4>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Hoverimg