import React, { useEffect, useState } from "react";
const Header = () => {

  const [offset, setOffset] = useState(0);
  useEffect(() => {
    window.onscroll = () => {
      setOffset(window.pageYOffset);
    };
  }, []);

  var headerStyle;

  if (offset > 50) {
    headerStyle = "navbar navbar-expand-lg navbar-light fixed-top py-3 bg-light";

  }
  else {
    headerStyle = "navbar navbar-expand-lg navbar-light fixed-top py-3 ";
  }




  return (


    <div className={headerStyle} id="scroll" >

      <div className="container-fluid"  >
        {/* <a className="navbar-brand" href="#">Start Bootstrap</a> */}

        <button className="navbar-toggler " type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ms-auto ">
            <li className="nav-item">
              <a className="nav-link navbar-brand" aria-current="page" href="#About-section">About Us</a>
            </li>
            <li className="nav-item">
              <a className="nav-link navbar-brand " href="#Service-section">Service</a>
            </li>
            <li className="nav-item">
              <a className="nav-link navbar-brand" href="#Portfolio-section">Protfolio</a>
            </li>
            <li className="nav-item">
              <a className="nav-link  navbar-brand" href="#contact-section" aria-disabled="true">Contact Us</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  )
}

export default Header